.container {
  height: 100%;
  padding-top: 30px;
}

.Santacontainer {
  height: 100%;
  padding-top: 30px;
  color: antiquewhite;
}

.Santacontainer::after{
  background-image: url("./Img/default_back_Santa.jpeg");
  content: "";
  opacity: 0.7;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: -1;
  background-size: full;
}

.movies {
display: grid;
grid-template-columns: repeat(auto-fill, minmax(500px, auto));
}

.footer {
display: flex;
justify-content: center;
height: 100px;
}

.list {
width: 40%;
display: flex;
flex-direction: row;
justify-content: space-between;
align-items: center;
}

.list a {
font-size: 30px;
}