.title {
  display: flex;
  justify-content: center;
  /* text-align: center; */
  margin: 4% 0px;
  font-size: 40px;
}

.title span {
  margin: 10px;
  text-shadow: 5px 5px rgb(136, 143, 141);
}

.titleBox:hover {
  transform: scale(1.1);
}

.titleImg {
  padding-top: 5px;
}

.footer {
  height: 50px;
  padding: 0px 30px;
  margin-top: 5%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgb(237,239,233);
}

.copyright {
  padding-top: 15px;
}

.copyright_letter {
  font-size: 15px;
  color: gray;
}